#main-field {
	width: 100%;
}

#main-button {
	height: 50px;
	font-size: 25px;
}

#error {
	width: 100%;
	max-width: 421px;
	max-height: 100px;
	overflow: hidden;
	word-break: break-word;
	text-overflow: ellipsis;
}

#info-area {
	width: 100%;
	max-width: 421px;
}

#main-title {
	width: 100%;
	color: black;
	font-weight: bold;
	font-size: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	line-height: 50px;
	margin-bottom: 10px;
}

#top {
	width: 100%;
	height: max-content;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#interactables {
	display: flex;
	justify-content: center;
	align-items: center;
}

#main {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

#interactable-buttons > button {
	padding: 0px;
	font-size: 18px;
	font-family: Arial !important;
	height: 35px;
	padding-left: 15px;
	padding-right: 15px;
	width: 140px;
}
#interactable-buttons > button > div:first-child {
	font-family: Arial !important;
}
#interactable-buttons > button:last-child > div:first-child {
	font-family: Arial !important;
	margin-right: 5px;
	margin-top: 4px;
}

.MuiLoadingButton-root .MuiCircularProgress-root {
	width: 32px !important;
	height: 32px !important;
	color: white !important;
}

.MuiMenuItem-root {
	display: flex !important;
	justify-content: center !important;
}

.MuiLoadingButton-loading {
	background-color: rgb(94, 94, 94) !important;
}

#header {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
	padding-top: 5px;
	box-sizing: border-box;
	background: rgb(167, 198, 230);
	background: linear-gradient(135deg, rgba(167, 198, 230, 1) -25%, rgba(255, 255, 255, 1) 100%);
	width: 100%;
	height: 100%;
}

#header > div {
	display: flex;
	flex-direction: column;
	width: max-content;
	height: max-content;
}
